  .mori-search {
    margin: 1em 0;
  }
  .mori-search-input {
    width: 15em;
    border-radius: 0.6em;
    padding: 0.75em 1em 0.5em 1em;
    background-color: white;
    margin: 0 auto;
  }
  .mori-search-input input[type="text"] {
    width: 100%;
    border: none;
    border-radius: 0;
    border-bottom: 2px dashed burlywood;
    padding: 0;
    padding-bottom: 0.25em;
    caret-color: green;
  }

  .mori-search-input input[type="text"]:focus {
    outline: none;
  }
  .vlgr {
    /*position: relative;*/
    display: inline-block;
    margin: 0.25em 0.5em;
    /*margin-bottom: 2em;*/
    /*padding: 0.75em;*/
    width: 3.75em;
    font-size: 0.85em;
  }
  .vlgr.selected img {
      background-color: yellowgreen;
  }
  .vlgr img {
      width: 100%;
      background-color: darkturquoise;
      /*background-color: lightseagreen;*/
      /*background-color: #1FC0B5;*/
      border-radius:50%;
  }
  .vlgr .name {
