@charset "UTF-8";
@font-face {
  font-family: 'Atsumare';
  src: url('./fonts/atsumare.ttf');
}

input[type='text'],
input[type='number'],
textarea {
  font-size: 1em;
}

.mori {
  text-align: center;
  font-family: 'Atsumare', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  margin: 0 auto;
  font-size: 1em;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.vResults {
    width: 100%;
}
.gResults {
    /*margin: 0 0.5em;*/
}

@media (min-width:768px)  {
  .mori {
    width: 27em;
  }
}
